.container {
  min-height: 700px;
  display: flex;
  justify-content: center;
  position: relative;
  @media (max-height: 768px) {
    min-height: calc(100vh);
  }
  .brandWrapper {
    position: absolute;
    top: 20px;

    left: 0;
  }
  .subContainer {
    @media (max-width: 959px) {
      padding-top: 64px;
    }
  }
}
